@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
/* colors */
.nav {
  position: fixed;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.1em 0;
  background-color: #000;
  z-index: 44; }
  .nav__icon {
    position: relative;
    margin-right: 0.5em;
    margin-left: auto;
    margin-top: 0.5em;
    display: inline-block;
    cursor: pointer; }
    .nav__icon__image {
      height: 45px;
      width: 45px;
      border-radius: 50%; }
  .nav__list {
    width: 100%;
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0;
    padding-left: 0;
    color: white; }
    .nav__list__group {
      position: relative;
      display: flex;
      padding: 0 1rem;
      z-index: 0; }
      .nav__list__group::after {
        position: absolute;
        top: 50%;
        right: 0;
        background-color: #cdcfd2;
        width: 1px;
        height: 35px;
        transform: translateY(-50%);
        content: ''; }
    .nav__list__logout {
      display: none; }
    .nav__list a {
      padding: 4px 16px;
      text-decoration: none;
      color: white;
      cursor: pointer; }
      .nav__list a:hover {
        color: #ddd; }
  .nav__logout {
    position: absolute;
    right: 20px;
    top: 40px;
    list-style: none;
    padding: 0 0 10px 0;
    border-radius: 5px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgba(255, 0, 0, 0.6); }
    .nav__logout-hide {
      display: none; }
  .nav__menu {
    position: absolute;
    right: 20px;
    top: 30px;
    list-style: none;
    padding: 0;
    border-radius: 5px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    width: 120px;
    background-color: rgba(0, 0, 0, 0.2);
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    .nav__menu__item {
      text-align: center;
      cursor: pointer; }
      .nav__menu__item:hover {
        color: #ccc; }

@media (max-width: 560px) {
  .nav {
    background-color: transparent; }
    .nav__list {
      display: none; }
      .nav__list__group {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        padding: 5px; }
        .nav__list__group:not(:first-child) {
          border-top: 1px solid white; }
        .nav__list__group::after {
          display: none; }
      .nav__list__logout {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 5px;
        padding: 5px;
        width: 100%;
        border-top: 1px solid red;
        border-radius: 4px;
        color: rgba(255, 0, 0, 0.6); }
    .nav__menu {
      display: flex; }
      .nav__menu-hide {
        display: none; }
    .nav__logout {
      display: none; } }

